<template>
    <div id="agent-index">
        <public-header title="区域代理管理"></public-header>
        <div class="content">
            <div class="group">
                <van-row gutter="20">
                    <van-col span="8" v-if="util.checkperm('agent.agent')">
                        <router-link tag="div" :to="util.getUrl({path: '/pages/plugin/agent/agent'})" class="merchant-item">
                            <div class="top">
                                <i class="icon icon-order c-primary"></i>
                            </div>
                            <div class="bottom">
                                <span>代理列表</span>
                            </div>
                        </router-link>
                    </van-col>
                    <van-col span="8" v-if="util.checkperm('agent.getcash')">
                        <router-link tag="div" :to="util.getUrl({path: '/pages/plugin/agent/getcash'})" class="merchant-item">
                            <div class="top">
                                <i class="icon icon-money c-danger"></i>
                            </div>
                            <div class="bottom">
                                <span>提现申请</span>
                            </div>
                        </router-link>
                    </van-col>
                    <van-col span="8" v-if="util.checkperm('agent.current')">
                        <router-link tag="div" :to="util.getUrl({path: '/pages/plugin/agent/current'})" class="merchant-item">
                            <div class="top">
                                <i class="icon icon-sponsor"></i>
                            </div>
                            <div class="bottom">
                                <span>账户明细</span>
                            </div>
                        </router-link>
                    </van-col>
                </van-row>
            </div>
        </div>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader,
		},
		methods: {

		}
	}
</script>

<style>
    #agent-index .content{
        background-color: #fff;
    }
    #agent-index .content .group{
        padding: 20px 15px;
    }
    #agent-index .content .group .van-row{
        text-align: center;
    }
    #agent-index .content .group .merchant-item{
        margin-bottom: 20px;
    }
    #agent-index .content .group .merchant-item .top{
        padding: 10px;
    }
    #agent-index .content .group .merchant-item .top .icon{
        font-size: 40px;
    }
    #agent-index .content .group .merchant-item .bottom{
        font-size: 14px;
        color: #7D8484;
    }
    #agent-index .content .group .merchant-item .top .icon-sponsor{
        color: #26A0DF;
    }
    #agent-index .content .group .merchant-item .top .icon-deletefill{
        color: #5C5C5C;
    }
    #agent-index .content .group .merchant-item .top .icon-survey{
        color: #F4D81C;
    }
</style>


